import { Injectable } from '@angular/core';
import { of, Observable } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { HandleErrorService } from './handle-error.service';


@Injectable({
    providedIn: 'root'
})
export class GetUpdateTestimonialVideoUrlService {

    getUpdateTestimonialVideoUrlServiceUrl = environment.contentServer + '/api/contentMgmt/updateTestimonialUrl';

    constructor(private httpClient: HttpClient, private handleErrorService: HandleErrorService) { }

    getUpdateTestimonialVideoUrlService(requestData): Observable<any> {

        const httpOptions: { headers; observe; } = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json'
            }),
            observe: 'response'
        };

        return this.httpClient.post<any>(this.getUpdateTestimonialVideoUrlServiceUrl, JSON.stringify(requestData), httpOptions)
            .pipe(
                tap(res => {
                    console.log(res);
                }),
                catchError(this.handleErrorService.handleError('getUpdateTestimonialVideoUrlService', []))
            );
    }
}
