import { Component, OnInit, AfterViewInit, OnDestroy, ViewChild, } from '@angular/core';
import { MalihuScrollbarService } from 'ngx-malihu-scrollbar';
import { MatDialog, MatDialogConfig, MatSlideToggleChange } from '@angular/material';
import { FormGroup, FormControl, FormBuilder } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';

import { ViewTestimonialVideoComponent } from '../shared/view-testimonial-video/view-testimonial-video.component';
import { SavedDialogComponent } from '../shared/saved-dialog/saved-dialog.component';
import { GetUpdateTestimonialVideoUrlService } from '../_service/get-update-testimonial-video-url.service';
import { AuthService } from '../_service/auth.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { TestimonialVideoService } from '../_service/testimonial-video.service';
import { GetCompanyListService } from '../companies/_services/get-company-list.service';
import { GetUpdateLogoStatusService } from '../_service/get-update-logo-status.service';
import { GetTermsTextService } from '../_service/get-terms-text.service';
import { UpdateTermsTextService } from '../_service/update-terms-text.service';




@Component({
  selector: 'app-landing-page-content',
  templateUrl: './landing-page-content.component.html',
  styleUrls: ['./landing-page-content.component.scss']
})
export class LandingPageContentComponent implements OnInit {
  @ViewChild('editableContent') editableContent;
  video: any;
  saveToggled: any[] = [];
  editedClause: any;
  showError;
  panelOpenState = false;
  editable = false;
  editModeEnable = false;
  disableMe: boolean;
  accessToken: string;
  sessionId: string;
  userID: string;
  moduleID: string;
  screenID: string;
  saveTitle = 'Your changes have been successfully saved';
  sucessTitle = 'Video updated Successfully';
  editClause = 'Diabos GDPR Policy<br><p></p> With the following information, we would like to give you an overview of how we will process your data and of your rights according to General Data Protection Regulation (GDPR). The details on what data will be processed and which method will be used depend significantly on the services applied for or agreed upon. Sources and Categories of Personal Data ConcernedWe process personal data that we obtain from our clients in the context of our business relationship.We process the following categories of personal dataPersonal data : Name, address, e-mail id, contact number(s), date of birth, gender, nationality, location, purchase and payment details, signatures, personal identification number, and passport number. Special categories : We do not seek to collect or otherwise Process your Sensitive Personal Information. Where we need to Process your Sensitive Personal Information for legitimate purpose, we do so in accordance with applicable laws. The Services are not intended for use by children. We do not collect or otherwise Process Personal Information about race, religion, sexual orientation or health or any other information that may be deemed to be sensitive under GDPR (collectively, “Sensitive Personal Information”) in the ordinary course of our business. Children: The DA Services are not intended for the use by Children below the age of 18 years.Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diamnonummy nibh …,Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diamnonummy nibh …';
  errorMessage = this.translate.instant('YOU_CAN_SELECT_ONLY_MAXIMUM_OF_COMPANIES');
  companyForm: FormGroup;
  videoForm: FormGroup;
  companyDetails = [];
  logosToUpdate = [];


  // public scrollbarOptions = {
  //   axis: 'y', theme: 'minimal-dark', setHeight: '350px'
  // };
  userRole: string;
  email: string;
  videoUrl: any;
  asdasd: any;
  videoValue: any;
  videoMsg: boolean;
  data;
  companyId: any;
  termsText = 'hard coded value';
  constructor(private formBuilder: FormBuilder, private translate: TranslateService, public dialog: MatDialog, private mScrollbarService: MalihuScrollbarService, private fb: FormBuilder, private getCompanyListService: GetCompanyListService, private getUpdateLogoStatusService: GetUpdateLogoStatusService, private getTermsTextService: GetTermsTextService, private updateTermsTextService: UpdateTermsTextService, private getUpdateTestimonialVideoUrlService: GetUpdateTestimonialVideoUrlService, private testimonialVideoService: TestimonialVideoService, private authService: AuthService, private spinnerService: Ng4LoadingSpinnerService) {
    this.editedClause = this.termsText;
    this.companyForm = this.fb.group({});

  }

  ngOnInit() {
    this.getCompanyList();
    this.getTermsText();
    this.getTestimonial();
    this.videoForm = this.formBuilder.group({
      testimonialVideo: [this.video]
    });
    this.userRole = this.authService.userRole;
    this.accessToken = this.authService.accessToken;
    this.sessionId = this.authService.sessionId;
    this.email = this.authService.email;
    this.moduleID = '11';
    this.screenID = '11';
    this.disableMe = true;
  }

  editedText($event) {
    this.editedClause = $event.target.innerText;
  }

  getTermsText() {
    this.getTermsTextService.getTermsTextService().subscribe(
      (res) => {
        if (!res.error) {
          this.termsText = res;
        } else {
          this.termsText = '';
        }
      }
    );
  }

  editedTerms(html) {
    this.updateTermsText(html);
  }
  updateTermsText(termsText) {
    this.spinnerService.show();
    const input = {
      'accessToken': this.accessToken,
      'email': this.email,
      'role': this.userRole,
      'sessionId': this.sessionId,
      'moduleId': this.moduleID,
      'screenId': this.screenID,
      'clientType': 'web',
      'termsText': termsText.content
    };

    this.updateTermsTextService.updateTermsTextService(input).subscribe((res: {}) => {
      if (res['status'] === 200) {
        const data = res['body'];
        if (data.status !== 'FAILED') {
          this.getTermsText();
          const dialogRef = this.dialog.open(SavedDialogComponent, {
            width: '30vw',
            disableClose: true,
            data: { title: this.saveTitle }
          });
        } else {

        }
        this.spinnerService.hide();
      }
    });
  }

  getTestimonial() {
    this.testimonialVideoService.getTestimonialVideoUrl().subscribe(
      (res) => {
        if (!res.error) {
          this.video = res;
        } else {
          this.video = '';
        }
      }
    );
  }

  changeStatus(i) {
    console.log('Company Details: ', this.companyDetails);
    if (this.companyForm.controls.sliderToggleControl['controls'][i].value === true) {
      if (this.saveToggled.length !== 0) {
        if (this.saveToggled.includes(i)) {
          this.saveToggled.filter(item => {
            if (item.index === i) {
              item.status = this.companyForm.controls.sliderToggleControl['controls'][i].value;
            }
          });
        } else {
          this.saveToggled.push({
            'index': i,
            'status': this.companyForm.controls.sliderToggleControl['controls'][i].value
          });
        }
      } else {
        this.saveToggled.push({
          'index': i,
          'status': this.companyForm.controls.sliderToggleControl['controls'][i].value
        });
      }

    } else if (this.companyForm.controls.sliderToggleControl['controls'][i].value === false) {
      const ind = this.saveToggled.findIndex(val => val.index === i);
      this.saveToggled.splice(ind, 1);
    }
    if (this.logosToUpdate.length !== 0) {
      const existingIds = this.logosToUpdate.map(obj => obj._id);
      if (!existingIds.includes(this.companyDetails[i]._id)) {
        this.logosToUpdate.push({
          '_id': this.companyDetails[i]._id,
          'logoEnabled': this.companyForm.controls.sliderToggleControl['controls'][i].value.toString()
        });
      } else {
        this.logosToUpdate.filter(item => {
          if (item._id === this.companyDetails[i]._id) {
            item.logoEnabled = this.companyForm.controls.sliderToggleControl['controls'][i].value.toString();
          }
        });
      }
    } else {
      this.logosToUpdate.push({
        '_id': this.companyDetails[i]._id,
        'logoEnabled': this.companyForm.controls.sliderToggleControl['controls'][i].value.toString()
      });
    }
    console.log(this.saveToggled);
    this.enableSaveButton();
    console.log('Logos to update: ', this.logosToUpdate);
  }

  addSliderToggle() {
    const arr = this.companyDetails.map(() => {
      return this.fb.control(false);
    });
    return this.fb.array(arr);
  }
  get sliderToggleControl() {
    return this.companyForm.get('sliderToggleControl');
  }


  saveButton() {
    if (this.editedClause && this.editedClause.trim()) {
      return false;
    } else {
      return true;
    }
  }

  openVideoDialog(): void {
    const dialogRef = this.dialog.open(ViewTestimonialVideoComponent, {
      disableClose: true
    });
    let videoLink = this.videoForm.controls['testimonialVideo'].value;

    if (!videoLink) {
      videoLink = this.video;
    }
    dialogRef.componentInstance.videoUrl = videoLink;
  }

  // ngAfterViewInit() {
  //   this.mScrollbarService.initScrollbar('#dialog-scroll', { axis: 'y', theme: 'dark-3' });
  // }

  // ngOnDestroy() {
  //   this.mScrollbarService.destroy('#dialog-scroll');
  // }

  updateVideoUrl() {
    this.videoValue = this.videoForm.value.testimonialVideo;
    if (!this.videoValue) {
      // this.videoValue = this.video;
    } else {
      this.spinnerService.show();
      const input = {
        'accessToken': this.accessToken,
        'email': this.email,
        'role': this.userRole,
        'sessionId': this.sessionId,
        'moduleId': this.moduleID,
        'screenId': this.screenID,
        'clientType': 'web',
        'testimonialLink': this.videoValue
      };

      this.getUpdateTestimonialVideoUrlService.getUpdateTestimonialVideoUrlService(input).subscribe((res: {}) => {
        if (res['status'] === 200) {
          const data = res['body'];
          if (data.status !== 'FAILED') {
            this.getTestimonial();
            const dialogRef = this.dialog.open(SavedDialogComponent, {
              width: '30vw',
              disableClose: true,
              data: { title: this.sucessTitle }
            });
          } else {

          }
        }
        this.spinnerService.hide();
      });
    }
  }

  getCompanyList() {
    this.spinnerService.show();
    const companyDetailsInput = {
      'email': this.authService.email,
      'role': this.authService.userRole,
      'moduleId': 'CP',
      'screenId': '12',
      'sessionId': this.authService.sessionId,
      'accessToken': this.authService.accessToken,
      'clientType': 'web'
    };

    this.getCompanyListService.getCompanyListService(companyDetailsInput).subscribe(
      (res) => {
        if (res['status'] === 200) {
          this.companyDetails = res.body.companyList;

          this.companyForm = this.fb.group({
            sliderToggleControl: this.addSliderToggle(),

          });

          this.saveToggled = [];
          this.companyDetails.forEach((company, index) => {
            if (company.logoEnabled === 'true') {
              this.companyForm.controls.sliderToggleControl['controls'][index].setValue(true);
              this.saveToggled.push({
                'index': index,
                'status': this.companyForm.controls.sliderToggleControl['controls'][index].value
              });
              this.logosToUpdate.push({
                '_id': company._id,
                'logoEnabled': this.companyForm.controls.sliderToggleControl['controls'][index].value.toString()
              });
            } else {
              this.companyForm.controls.sliderToggleControl['controls'][index].setValue(false);
              this.logosToUpdate.push({
                '_id': company._id,
                'logoEnabled': this.companyForm.controls.sliderToggleControl['controls'][index].value.toString()
              });
            }
          });
          this.enableSaveButton();
        }
        this.spinnerService.hide();
      });

  }
  saveStatus() {
    console.log('in save status:', this.logosToUpdate);
    this.spinnerService.show();
    const input = {
      'accessToken': this.accessToken,
      'email': this.email,
      'role': this.userRole,
      'sessionId': this.sessionId,
      'moduleId': this.moduleID,
      'screenId': this.screenID,
      'clientType': 'web',
      'logosToUpdate': this.logosToUpdate
    };

    this.getUpdateLogoStatusService.getUpdateLogoStatusService(input).subscribe((res: {}) => {
      if (res['status'] === 200) {
        const data = res['body'];
        if (data.status !== 'FAILED') {
          this.getCompanyList();
          const dialogRef = this.dialog.open(SavedDialogComponent, {
            width: '30vw',
            disableClose: true,
            data: { title: this.saveTitle }
          });
        } else {

        }
        this.spinnerService.hide();
      }
    });
  }

  enableSaveButton() {
    if (this.saveToggled.length >= 3 && this.saveToggled.length <= 15) {
      this.disableMe = false;
    } else {
      this.disableMe = true;
    }
    if (this.saveToggled.length > 15) {
      this.showError = true;
    } else {
      this.showError = false;
    }
  }
}



